import React from 'react'
import Nav from '../../components/Nav'
import './style.css';
import avatar from './avatar.jpg'

function Home() {
    return (
        <div>
   
            <section id="home">
                <div class="home__container">
                    <img src={avatar} alt="jun's profile" class="home__avatar"/>
                    <h1 class="home__title">
                        Hello,
                        
                        
                        <br/>I'm Dream Coder</h1>
                    <h2 class="home__description">Lorem ipsum dolor sit, amet consectetur adipisicing elit. Asperiores eveniet ipsa reiciendis laudantium ipsum provident velit sed? Molestiae, officia. Dicta laborum animi ipsam officiis expedita harum ea iure similique tempore.</h2>
                    <button class="home__contact">Contact Me</button>
                </div>
            </section>
        </div>
    )
}

export default Home