import React from 'react'
import './style.css'
import Button from 'react-bootstrap/Button';
function Write() {
    const submit = () =>{
        alert("text")
    }

    return (
        <div className="write">
            <div className="write__content">
                {/* write example */}
                <div className="write__example">
                   게시판 글쓰기</div>
                    {/* 제목  */}
                    <input className="write__input__title" type="text" placeholder="제목을 입력하세요"/>
                {/* 메인컨텐츠     */}
                <textarea
                    name="write__input__content"
                    id="write__input__content"
                    cols="100"
                    rows="24"
                    placeholder="내용을 입력하세요"></textarea>

                {/* 글쓰기 튼 */}
                <button onClick={submit} className="write__upload__button">글쓰기</button>

            </div>

        </div>
    )
}

export default Write
