import React, {useState,useEffect} from 'react'
import logo from './onlylogo.png'
import './style.css'
import {Link} from "react-router-dom"
function Nav() {
    const [show, setShow] = useState (false);
    const navbarscroll = () =>{
       if (window.scrollY > 100 ){
           setShow(true);
       }else{
           setShow(false);
       }
    }
    window.addEventListener('scroll',navbarscroll);
    return (
        <nav className={`${show === true && 'navbar__white' || 'navbar'}`}>

            <div class="navbar__logo">
                <img src={logo} alt="" width="16px" height="16px"/>
                <a className="navbar__logo__name" href="#">Jun</a>
            </div>

            <ul class="navbar__menu">
                <li class="navbar__menu__item"><Link to="/Home">Home</Link></li>
                <li class="navbar__menu__item"><Link to="/Board">Board</Link></li>
                <li class="navbar__menu__item"><Link to="/skills">Skills</Link></li>
                <li class="navbar__menu__item"><Link to="/my__work">My work</Link></li>
                <li class="navbar__menu__item"><Link to="/testimonial">Testimonial</Link></li>
                <li class="navbar__menu__item"><Link to="/contact">Contact</Link></li>
               
            </ul>

            <button class="navbar__toggle-btn">
                <i class="fas fa-bars"></i>
            </button>

        </nav>

    )
}

export default Nav