import React from 'react'
import avatar from './avatar.jpg'
function Testimonial__speech() {
	return (
		<div>
			<div class="testimonial">
                <img src={avatar} alt="People" class="testimonial__avatar"/>
                <div class="testimonial__speech-bubble">
                    <p>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Tempore voluptas labore in veritatis perspiciatis maiores pariatur aspernatur minima iure. Quaerat eligendi, deleniti recusandae perspiciatis illo quisquam? Eligendi expedita dolore aliquam?</p>
                    <p class="name"><a href="$">Ellie</a> /samsung</p>
                </div>
            </div>
		</div>
	)
}

export default Testimonial__speech
