import React from 'react'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import './style.css'
import About__majors from '../../components/About__me'
import About__jobs from '../../components/About__jobs'

function About() {
    return (
        <> 
				< section id = "about" class = "section section__container  " > 
				<h1>About me</h1>
        <p>I'm studying front-end development, and I'm focusing on React. I majored in
            Marketing. I will become a developer with the best performance.</p>
        <div class="about__majors">

            <About__majors/>
            <About__majors/>
            <About__majors/>

        </div>
        <div className="about__jobs">
            <About__jobs/>
            <About__jobs/>

        </div>

    </section>

</>
    )
}

export default About
