import React from 'react'
import './style.css'
import 동아대로고 from './동아대로고.jpg'
function About__jobs() {
	return (
		<div>
			<div class="job">
        <img src={동아대로고} alt="Dong-A University" class="job__logo"/>
        <div class="job__description">
        
        <p class="job__name">Dong-A University</p>
        <p class="job__period ">2021.03.01~</p>
    </div>
    </div>
		</div>
	)
}

export default About__jobs
