import React from 'react'
import  './style.css'
function Edit() {
	return (
		<div className="edit">
				<div className="edit__content">
						{/* edit example */}
						<div className="edit__example">
							 게시판 수정하기</div>
								
								{/* 제목  */}

								<input className="edit__input__title" type="text" placeholder="제목을 수정하세요"/>
						{/* 메인컨텐츠     */}
						<textarea
								name="edit__input__content"
								id="edit__input__content"
								cols="100"
								rows="24"
								placeholder="내용을 수정하세요"></textarea>

						{/* 글쓰기 튼 */}
						<div className="edit__buttons">
						<button>취소</button>
						<button>수정하기</button>
						</div>

				</div>

		</div>
)
}

export default Edit
