import React from 'react'
import './style.css'
import {Link} from "react-router-dom"

function Login() {
    return (
        <div className="login">
            <div className="content">
                <div className="email__content">
                    <label for="email" id="email">Enter your email:</label><br/>
                    <input type="email" placeholder="xxxx@example.com" id="email__box"/><br/>
                </div>
                <div className="password__content">
                    <label for="password" id="password">Enter your password:</label><br/>
                    <input type="password" placeholder="******" id="password__box"></input>
                </div>
                <button className="login__button btn btn-danger">Login</button>
            </div>

        </div>
    )
}

export default Login
